import React from 'react';
import { StaticImage } from 'gatsby-plugin-image';

import SEO from '../components/SEO';
import CTA from '../components/CTA';

const AboutPage = () => {
  return (
    <>
      <div className="contain-sm my-8 md:my-16">
        <h1 className="flex items-center text-3xl md:text-4xl md:text-5xl">
          Hi there!
          <span className="w-12 mx-3">
            <StaticImage
              src="../images/wave.png"
              alt="Waving Hand Emoji"
              width={100}
              quality={100}
              layout="constrained"
              placeholder="blurred"
            />
          </span>
          I&apos;m Safdar.
        </h1>
        <p className="text-mdgray my-8 md:text-lg">
          I'm a passionate freelance software developer based in Karachi,
          Pakistan. I thrive on creating dynamic and engaging web experiences
          that prioritize user satisfaction, accessibility, and performance.
        </p>
        <p className="text-mdgray my-8 md:text-lg">
          With a deep love for building innovative digital solutions, I
          collaborate with companies and agencies worldwide to design and
          develop websites and web applications that not only look great but
          also function seamlessly. My focus on user experience (UX) ensures
          that every project I undertake is intuitive, inclusive, and impactful.
        </p>
        <p className="text-mdgray my-8 md:text-lg">
          I bring a strong technical foundation, a keen eye for detail, and a
          commitment to excellence to every project. Whether it's a sleek
          website for a growing startup, a robust application for a large-scale
          enterprise, or anything in between, I'm dedicated to turning ideas
          into reality.
        </p>
        <h2 className="text-xl md:text-3xl mb-4">Tools &amp; Technologies</h2>
        <p className="text-mdgray my-8 md:text-lg">
          Over the years, I've honed my skills across a wide range of tools and
          technologies, always staying ahead of industry trends to deliver
          cutting-edge solutions. Here's a snapshot of what I bring to the
          table:
        </p>
        <div className="max-w-md">
          <ul className="ml-5 flex flex-wrap md:text-lg text-mdgray list-disc">
            <li className="w-3/6 p-2">HTML</li>
            <li className="w-3/6 p-2">CSS</li>
            <li className="w-3/6 p-2">JavaScript</li>
            <li className="w-3/6 p-2">Bootstrap</li>
            <li className="w-3/6 p-2">React</li>
            <li className="w-3/6 p-2">Redux</li>
            <li className="w-3/6 p-2">Gatsby</li>
            <li className="w-3/6 p-2">GraphQL</li>
            <li className="w-3/6 p-2">Next.js</li>
            <li className="w-3/6 p-2">Node.js</li>
            <li className="w-3/6 p-2">Express</li>
            <li className="w-3/6 p-2">MongoDB</li>
          </ul>
        </div>
        <p className="text-mdgray my-8 md:text-lg">
          This toolkit reflects my versatility and adaptability as a developer,
          but I'm always eager to expand it. I welcome the opportunity to learn
          and implement new technologies to meet the unique needs of any
          project.
        </p>
        <h2 className="text-xl md:text-3xl mb-4">
          Let's Build Something Amazing
        </h2>
        <p className="text-mdgray my-8 md:text-lg">
          I believe that every project is an opportunity to create something
          truly special. If you're looking for a developer who values
          collaboration, creativity, and quality, I'd love to hear from you.
          Let's work together to craft exceptional digital experiences that
          leave a lasting impression!
        </p>
      </div>
      <section className="contain-sm mt-32">
        <CTA
          title="Ready to get started?"
          description="You know about me, let's talk about you."
          buttonText="Get In Touch"
        />
      </section>
    </>
  );
};

export default AboutPage;
export const Head = () => (
  <SEO
    title="About"
    description="I'm a passionate freelance software developer.
                I thrive on creating dynamic and engaging web experiences
                that prioritize user satisfaction, accessibility, and performance."
  />
);
